section{
    padding-bottom: 40px;
    padding-top: 40px;
    margin: 0 auto;
    background: var( --secondary-light-background);
}

.contacts-item{
    width: 100%;
    
    padding: 40px 32px;
    background: var(--light-txt-color);
box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
border-radius: 24px;
}
@media screen and (min-width:768px) {
    .contacts-item{
    
    width: 224px;

}
.contacts-item + .contacts-item{
    margin-left: 32px;
}
.contacts-list{
    display: flex;
    justify-content: center;
}
}
@media screen and (min-width:1280px){
    .contacts-item{
    width: 395px;
}
}
@media screen and (max-width:767px) {
.contacts-item:not(:first-child){
    margin-top: 20px;
}
}
.contacts-title{
    font-family: 'DM Sans';
font-style: normal;
font-weight: 500;
    color: var(--light-txt-color);
    width: 80px;
    background:var(--dark-product2-background);
border-radius: 4px;
    font-size: 12px;
    line-height: 22px;
    align-items: center;
    text-align: center;
    letter-spacing: 0.04em;
}
@media screen and (min-width:768px){
    .contacts-title{
        font-size: 16px;
        line-height: 30px;
    }
}
.contacts-place{
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    margin-top: 20px;
    font-size: 12px;
line-height: 22px;
letter-spacing: 0.04em;
color: var(--secondary-txt-color)
}
@media screen and (min-width:768px){
    .contacts-place{
        font-size: 14px;
        line-height: 26px;
    }
}
@media screen and (min-width:768px){
    .contacts-place{
        font-size: 16px;
        line-height: 30px;
    }
}
.contacts-description{
  word-break: normal;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color:var(--primarily-txt-color);
    margin-top: 8px;
    border-bottom: 1px solid #E1E1E1;
    padding-bottom: 24px;   
}
@media screen and (min-width:1280px){
    .contacts-description{
        font-size: 14px;
        line-height: 23px;
    }
}
@media screen and (min-width:1280px){
    .contacts-description{
        font-size: 16px;
        line-height: 29px;
    }
}

.contacts-day{
    font-family: 'DM Sans';
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 22px;
color:var(--secondary-txt-color)   
}
@media screen and (min-width:768px){
    .contacts-day{
        font-size: 14px;
        line-height: 26px;
    }
}
@media screen and (min-width:1280px){
    .contacts-day{
        font-size: 16px;
        line-height: 30px;
    }
}
.contacts-day:not(:first-child){
    margin-top: 8px;
}
.contacts-container{
margin-top: 24px;
border-bottom: 1px solid #E1E1E1;
    padding-bottom: 24px;
}

.contacts-time{
    font-family: 'DM Sans';
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 22px;
color: var(--accent-title-color)
}
@media screen and (min-width:768px){
    .contacts-time{
        font-size: 14px;
        line-height: 26px;
    }
}
@media screen and (min-width:1280px){
    .contacts-time{
        font-size: 16px;
        line-height: 30px;
    }
}
.contacts-social{
margin-top: 24px;
}
.contacts-link{
    display: flex;
    font-family: 'DM Sans';
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 18px;
transition: color 550ms cubic-bezier(0.4, 0, 0.2, 1), 
background-color 550ms cubic-bezier(0.4, 0, 0.2, 1), transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

color: var( --primarily-txt-color)
}
@media screen and (min-width:768px){
    .contacts-link{
        font-size: 14px;
        line-height: 26px;
    }
}
@media screen and (min-width:1280px){
    .contacts-link{
        font-size: 16px;
        line-height: 29px;
    }
}
.contacts-link:hover,
.contacts-link:focus{
    color: var(--title-color);
}
.contacts-link:not(:first-child){
    margin-top: 8px;
}
.sub-title{
    width: 130px;
    background: var( --dark-product3-background);
border-radius: 4px;
}
.contacts-btn{
    justify-content: center;
    width: 160px;
    height: 44px;
    border: none;
    background: var(--title-color);
border-radius: 22px;
font-family: 'DM Sans';
font-style: normal;
font-weight: 700;
font-size: 16px;
line-height: 21px;
display: flex;
align-items: center;
text-align: center;
color: var(--light-txt-color);
transition: color 550ms cubic-bezier(0.4, 0, 0.2, 1), 
background-color 550ms cubic-bezier(0.4, 0, 0.2, 1), 
transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
@media screen and (min-width:768px){
    .contacts-btns{
        display: flex;
        justify-content: center;
    }
   
} 
@media screen and (min-width:768px){
    .contacts-btn + .contacts-btn-2
    {
       margin-left: 14px;
      
    }
   
}
@media screen and (min-width:768px){
    .contacts-btn{
        width: 191px;
    }
}
@media screen and (min-width:1280px){
    .contacts-btn{
        width: 198px;
    }
}
.contacts-btns{
    margin-top: 32px;
}
.contacts-btn-2 {
    justify-content: center;
    width: 180px;
    height: 44px;
    border: none;
    background: var(--light-txt-color);
     color: var(--title-color);
border-radius: 22px;
font-family: 'DM Sans';
font-style: normal;
font-weight: 700;
font-size: 16px;
line-height: 21px;
display: flex;
align-items: center;
text-align: center;

transition: color 550ms cubic-bezier(0.4, 0, 0.2, 1), 
background-color 550ms cubic-bezier(0.4, 0, 0.2, 1), 
transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
   
   
}
@media screen and (max-width:767px){
    .contacts-btn-2{
        margin: 0 auto;
       margin-top: 10px;
       width: 160px;
    }
    .contacts-btn{
        margin: 0 auto;
    }  
    
}
@media screen and (max-width:1279px){
    .contacts-btn-2{
       width: 174px;
    }
}
.contacts-btn-2:hover,
.contacts-btn-2:focus{
    transform: scale(1.05);
    box-shadow: 0px 2px 2px rgb(0 0 0 / 25%);
    color: var(--light-txt-color);
    background: var(--title-color) ; 
}
.contacts-btn:hover,
.contacts-btn:focus{
    transform: scale(1.05);
    box-shadow: 0px 2px 2px rgb(0 0 0 / 25%);
    color: var(--title-color);
    background: var(--light-txt-color) ; 
}
